import React from "react";
import userPhoto from "./img/user.png";
import style from "./Users.module.css";
import {NavLink} from "react-router-dom";
import Preloader from "../Common/Preloader/preloader";

const User = ({isFetching, inProgress, follow, currentPage, pageSize, users}) => {
    return (
        <div className={style.Users}>
            {isFetching ? <Preloader/>
                :
                <NavLink to={'/Профиль/' + users.id} className={style.User}>
                            <img src={users.photos.small || userPhoto}
                                 alt='Лого'/>

                    <span className={style.Name}>{users.name}</span>


                        <button disabled={inProgress.some(id => id === users.id)}
                                onClick={() => {
                                    follow(users.id, users.followed, currentPage, pageSize)
                                }}>
                            {(users.followed) ? 'Отписаться' : 'Подписаться'}
                        </button>

                    <span className={style.Status}>{users.status}</span>


                </NavLink>}
        </div>
                )}

                export default User;