import React from "react";
import EditProfile from "./EditProfile";
import {connect} from "react-redux";
import {getProfileUser, updateFullName, uploadPhoto} from "../../../Redux/profileReducer";
import Preloader from "../Common/Preloader/preloader";
import {compose} from "redux";
import {WithAuthRedirect} from "../HOC/WithAuthRedirect";

function EditProfileContainer(props) {

    const onSubmit = (formData) => {
        props.updateFullName(formData)
    }
    return (<div>
        {!props.profile ? <Preloader/> :
        <EditProfile initialValues={props.profile}
                     onSubmit={onSubmit}
                     profile={props.profile}
                     uploadPhoto = {props.uploadPhoto}
        />}
    </div>  )
}

const mapStateToProps = (state) => {
   return {
       profile: state.auth.authProfile,
 }
}
export default compose(WithAuthRedirect,
connect(mapStateToProps,{uploadPhoto,getProfileUser,updateFullName}))(EditProfileContainer)