import React from 'react';
import style from './profile.module.css';
import Preloader from "../Common/Preloader/preloader";
import EditProfileImage from "./ProfileInfo/ProfileStatus/EditProfileImage";
import EditProfileStatus from "./ProfileInfo/ProfileStatus/EditProfileStatus";
import ProfileStatus from "./ProfileInfo/ProfileStatus/ProfileStatus";

const Profile = (props) => {
    let owner;
        !!props.id ? ((props.id === props.authId) && (owner = true))
                   : (owner = true)

    return (
        <div className={style.Content}>
            {(!props.profile || props.profileIsFetching) ? <Preloader/> :
                <div>
                 <EditProfileImage {...props} />
                 <div className={style.fullName}>
                        <b>Имя:</b> {props.profile.fullName}
                 </div>
                 <EditProfileStatus    owner={owner}
                                       updateStatus={props.updateStatus}
                                       profileStatus={props.status}
                                       StatusUpdateMessage={props.StatusUpdateMessage}
                                       profileMessageApi={props.profileMessageApi}

                 />

                  <div className={style.profileInfo}>
                 <ProfileStatus {...props}/>
                  </div>
                </div>
                }
        </div>
    )
}

export default Profile;