
import React from "react";
import {connect} from "react-redux";
import News from "./News";
import {getNewsApi} from "../../../Redux/newsReducer";

const NewsContainer = (props) => {
        return <News newsBlock={props.newsBlock}
              count = {props.count}
              totalResults={props.totalResults}
              getNewsApi={props.getNewsApi}/>
}

let mapStateToProps = (state) => {
    return {
        newsBlock: state.NewsPage.newsBlock,
        count: state.NewsPage.count,
        totalResults: state.NewsPage.totalResults
    }
}
export default connect (mapStateToProps, {getNewsApi})(NewsContainer);