import * as axios from "axios";

const instance = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {'API-KEY': '3f7c7d58-d7f2-4bf3-9f16-f169a0f9cb52'}
});

export const usersApi = {
    getUsers(currentPage = 1, pageSize = 10) {
        return instance.get(`users?page=${currentPage}&count=${pageSize}`,
        ).then(response => response.data)
    },
    getAuthMe() {
        return instance.get(`auth/me`).then(response => response.data)
    },
    postFollow(usersId) {
        return instance.post(`follow/${usersId}`)
    },
    deleteFollow(usersId) {
        return instance.delete(`follow/${usersId}`)
    },

    async requestFriends(count = 15) {
        let response = await instance.get(`users?count=${count}&friend=true`,)
        if (response.data.error === null) {
            return response.data
        }
    }
}

export const profileApi = {
    apiUploadPhoto(photo){
        let formData = new FormData();
        formData.append('image:small', photo)
        return instance.put('profile/photo',formData,{
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        })
    },

    apiGetProfileUser(userUrlId) {
        return instance.get(`profile/${userUrlId}`)

    },
    getStatus(userUrlId) {
        return instance.get(`profile/status/${userUrlId}`)
    },
    updateStatus(status) {
        return instance.put('profile/status/', {status: status})
    },
    apiUploadFullName(profile) {
        return instance.put('profile', profile)
    }
}

export const authApi = {
    loginOn(email, password, rememberMe = false,captcha) {
        return instance.post('auth/login', {email, password, rememberMe,captcha})
    },
    loginOut() {
        return instance.delete('auth/login')
    },
    getCaptchaUrl(){
        return instance.get('security/get-captcha-url')
    }
}

