import React, {useEffect, useState} from "react";
import style from './../../profile.module.css'


const EditProfileStatus = ({owner,updateStatus, profileStatus,profileMessageApi}) => {
    let [editMode, setEditMode] = useState(false);
    let [status, setStatus] = useState(profileStatus);

    const setTrueEditMode = () => {
        owner && setEditMode(true);
    };
    const setFalseEditMode = () => {
        setEditMode(false);
        updateStatus(status);
    };
    const enteringText = (text) => {
        setStatus(text.currentTarget.value)
    };
    useEffect(() => {
        setStatus(profileStatus)
    }, [profileStatus]);


    return (
        <div className={style.statusBlock}>
            {(!editMode) ?
                <span onClick={setTrueEditMode} className={style.updateStatus}> <b>Статус:</b> {
                    profileStatus || 'нет статуса'}
                    {!!profileMessageApi && <span className={style.popUp} >{profileMessageApi}</span>}
                </span>

                :
                <input onChange={enteringText} autoFocus={true} value={status}
                       onBlur={setFalseEditMode}/>
            }
        </div>
    )
}
export default EditProfileStatus;