import React, {useEffect, useState} from "react"
import Profile from "./profile"
import {connect} from "react-redux"
import {
    getProfileUser,
    getStatus,
    updateStatus,
    uploadPhoto,
} from "../../../Redux/profileReducer"
import {withRouter} from "react-router-dom";
import {WithAuthRedirect} from "../HOC/WithAuthRedirect";
import {compose} from "redux";

const ProfileContainerHook = ({errorSetProfile,getStatus,getProfileUser,...props})=> {
    let [UrlId,setUrlId] = useState()

    useEffect( ()=>{
        setUrlId(props.match.params.userId)
    },[props.match.params.userId])

    useEffect(()=>{
        getProfileUser(UrlId)
    },[getProfileUser,UrlId])

    return <div>
    {errorSetProfile?
            <div>Произошла ошибка во время выполнения операции, повторите позднее</div>
            :<Profile {...props}/> }
    </div>


}

let mapStateToProps = (state) => ({
    profile: state.ProfilePage.Profile,
    status: state.ProfilePage.Status,
    authId: state.auth.id,
    profileIsFetching: state.ProfilePage.profileIsFetching,
    profileMessageApi: state.ProfilePage.profileMessageApi,
    errorSetProfile: state.ProfilePage.errorSetProfile
})

export default compose(WithAuthRedirect,withRouter,connect(mapStateToProps,{uploadPhoto,getProfileUser,getStatus,updateStatus})

)(ProfileContainerHook);
