import React from "react";
import style from './../newsPage.module.css'

const NewsPage = (props) => {
    return <a href={props.url} className={style.NewsPage}>
                 <img src={props.image} alt={'img'}  className={style.NewsImg}/>
                 <span className={style.NewsTitle}>{props.title}</span>
           </a>
}

export default NewsPage;