import * as axios from "axios";

const instance = axios.create ({
    baseURL: 'http://newsapi.org/v2',
    headers: {'X-Api-Key': "814c7db8c46245d3bc4e871fd646ecad"}

    }
)

export const ApiNews = {
    getNews(pageSize=10,currentPage=1){
        return instance.get(`top-headlines?country=ru&pageSize=${pageSize}&page=${currentPage}`)
    },
}