import {authApi, profileApi, usersApi} from "../API/api"
import {stopSubmit} from "redux-form"
import {requestFriendsAC, setFriends} from "./navbarReducer";
import {setUserProfile} from "./profileReducer";

let SET_USER_DATA = '/auth/SET_USER_DATA';
let setAuthProfile = '/auth/setAuthProfile';
let setCaptcha = '/auth/setCaptcha';

let initialState = {
    id: null,
    email: null,
    login: null,
    isFetching: null,
    isAuth: false,
    authProfile: null,
    captcha: null
};

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case '/auth/SET_USER_DATA':
            return {...state, ...action.data, isAuth: action.isAuth}
        case '/auth/setAuthProfile':
            return {...state, authProfile: action.authProfile}
        case '/auth/setCaptcha':
            return {...state, captcha: action.captcha}
        default:
            return state
    }
};
const setCaptchaUrl = (captcha) => ({type: setCaptcha, captcha})
export const setUserData = (id, email, login, isAuth) => ({
    type: SET_USER_DATA,
    data: {id, email, login}, isAuth
});
const setAuthProfileAC = (authProfile) => ({type: setAuthProfile, authProfile})
export const getLogin = () => async (dispatch) => {
    let response = await usersApi.getAuthMe()
    if (response.resultCode === 0) {
        let {id, email, login} = response.data;
        dispatch(setUserData(id, email, login, true))
        dispatch(requestFriendsAC())
        dispatch(getAuthProfile(response.data.id))
    }
};
const getAuthProfile = (AuthProfile) => async (dispatch) => {
    let response = await profileApi.apiGetProfileUser(AuthProfile)
    if (response.status === 200) {
        dispatch(setAuthProfileAC(response.data))
    }
}
export const setLogin = (email, password, rememberMe,captcha) => async (dispatch) => {
    let response = await authApi.loginOn(email, password, rememberMe,captcha)

    if (response.data.resultCode === 0) {
        dispatch(getLogin())
    } else {
        if (response.data.resultCode === 10 ){dispatch(getCaptcha())}
        dispatch(stopSubmit('login', {_error: response.data.messages[0]}))
    }
    debugger;
};
export const disLogin = () => async (dispatch) => {
    let response = await authApi.loginOut();

    if (response.data.resultCode === 0) {
        dispatch(setUserData(null, null, null, false))
        dispatch(setFriends(null))
        dispatch(setUserProfile(null))
    }
};
export const getCaptcha = () => async (dispatch) => {
    const response = await authApi.getCaptchaUrl()
        dispatch(setCaptchaUrl(response.data.url))

}

export default authReducer