import {ApiNews} from "../components/html_components/News/ApiNews";

const SetNews = '/NewsReducer/SetNews';
const totalResults = '/NewsReducer/totalResults'

let initialState = {
    newsBlock : null,
    count: 4,
    totalResults: null
}

const NewsReducer = (state= initialState, action) => {
    switch (action.type) {
        case '/NewsReducer/SetNews':
            return {...state, newsBlock: action.newsBlock}
        case '/NewsReducer/totalResults':
            return {...state, totalResults: action.count}
        default:
            return state;
    }
}
const totalResultsAC = (count) => ({type: totalResults, count})
const setNewsBlock = (newsBlock) => ({type: SetNews, newsBlock})
export const getNewsApi = (pageSize, page) => async(dispatch) => {
    let response = await ApiNews.getNews(pageSize, page)
    dispatch(setNewsBlock(response.data))
    dispatch(totalResultsAC(response.data.totalResults))
}

export default NewsReducer;