import {profileApi} from "../API/api";


const SetUserProfileAC = '/profileReducer/SetUserProfileAC';
const SetUserStatus = '/profileReducer/SetUserStatus';
const profileIsFetching = '/profileReducer/profileIsFetching';
const profileMessageApi = '/profileReducer/profileMessageApi';
const errorSetProfile = '/profileReducer/errorSetProfile';

let initialState = {

    Profile: null,
    Status: '',
    profileIsFetching: false,
    profileMessageApi: null,
    errorSetProfile: false
};

const profileReducer = (state = initialState, action) => {
    switch (action.type) {

        case '/profileReducer/SetUserProfileAC':
            return {...state, Profile: action.profile}
        case '/profileReducer/SetUserStatus':
            return {...state, Status: action.status}
        case '/profileReducer/profileIsFetching':
            return {...state, profileIsFetching: action.value}
        case '/profileReducer/profileMessageApi':
            return {...state, profileMessageApi: action.messageApi}
        case '/profileReducer/errorSetProfile':
            return {...state, errorSetProfile: action.errorSetProfile}
        default:
            return state;
    }
};


export const setUserProfile = (profile) => ({type: SetUserProfileAC, profile});
export const setUserStatusAC = (status) => ({type: SetUserStatus, status});
export const profileIsFetchingAC = (value) => ({type: profileIsFetching, value})
export const profileMessageApiAC = (messageApi) => ({type: profileMessageApi, messageApi })
export const errorSetProfileAC = (error) => ({type: errorSetProfile, error})

export const getProfileUser = (userUrlId) => async (dispatch) => {
    if (!!userUrlId) {
        dispatch(setUserProfile(null))
        dispatch(profileIsFetchingAC(true))

        let response = await profileApi.apiGetProfileUser(userUrlId)
        let responseStatus = await profileApi.getStatus(userUrlId)

        if (response.status === 200 && responseStatus.status === 200) {
            dispatch(setUserProfile(response.data))
            dispatch(profileIsFetchingAC(false))
            dispatch(setUserStatusAC(responseStatus.data))
            }
        else {
                dispatch(setUserProfile(null))
                dispatch(setUserStatusAC(null))
                dispatch(errorSetProfileAC(true))
            }
    }
}

export const getStatus = (userUrlId) => async (dispatch) => {
    if(!!userUrlId) {dispatch(setUserProfile(null))
        let response = await profileApi.getStatus(userUrlId)
        if (response.status === 200) {
            dispatch(setUserStatusAC(response.data))
        }
        else{
            dispatch(setUserProfile(null))
        }
    }

};
export const updateStatus = (status) => async (dispatch) => {
        if (!!status) {
            let response = await profileApi.updateStatus(status)
            if (response.data.resultCode === 0) {
                dispatch(setUserStatusAC(status))
                dispatch(profileMessageApiAC('Успешно'))
                setTimeout(() => {
                    dispatch(profileMessageApiAC(null))
                }, 3000)
            } else {
                dispatch(profileMessageApiAC(response.data.messages))
                setTimeout(() => {
                    dispatch(profileMessageApiAC(null))
                }, 3000)
            }
        }
};

export const uploadPhoto = (photo) => async() => {
    await profileApi.apiUploadPhoto(photo);

}
export const updateFullName = (profile) => async(dispatch, getState) => {
    const userId = getState().auth.id
    let response = await profileApi.apiUploadFullName(profile)
    if(response.status === 200){
        dispatch(getProfileUser(userId))
    }
}

export default profileReducer;