import React, {useEffect, useState} from "react";
import NewsPage from "./NewsPage/NewsPage";
import style from "./newsPage.module.css";
import {NavLink} from "react-router-dom";
import cn from 'classnames';
import Preloader from "../Common/Preloader/preloader";

const News = ({newsBlock,count,totalResults,getNewsApi}) => {
    let [currentPage, setPageState] = useState(1);
    useEffect(()=>{
        getNewsApi(count,currentPage)},[getNewsApi,count,currentPage]
    )
    let setNewPage = (page) => {
        setPageState(page)

    }
    let countPages = Math.ceil(totalResults / count)

    let prevPage = 1;
    let nextPage = 5;

    if (2 < currentPage && currentPage < (countPages-2)){
        prevPage = currentPage-2;
        nextPage = currentPage+2;
    }
    if (currentPage >= (countPages-2)){
        prevPage = countPages-4;
        nextPage = countPages;
    }
    let pages = [];

    for (let i = 1; i <= countPages; i++){pages.push(i);}

    return <div className={style.News}>

            <h2>Новости</h2>
        <div className={style.NewsBlock}>
        {!newsBlock ? <Preloader />:
            <span>
                {newsBlock.articles.map
                (news => <NewsPage key={news.title}
                                   title={news.title}
                                   image={news.urlToImage}
                                   url={news.url}/>)}
            </span>}
        </div>
            <div className={style.newsPages}>
                {pages.filter(page =>
                page>=prevPage && page <= nextPage
                ).map(page => <NavLink to={`/Новости/${page}`} onClick={()=>setNewPage(page)}
                                            activeClassName={cn ({[style.selectedPage] : currentPage===page
                        })} key={page}>{page}</NavLink>)}
            </div>
            </div>
}

export default News;