import React from "react";
import style from "../Profile/profile.module.css";
import {CreateField, Input, Textarea} from "../Common/FormsControl/FormsControl";
import {Field, reduxForm} from "redux-form";


const EditProfile = ({handleSubmit,profile,uploadPhoto}) => {
    const mainPhotoSelected = (el) => {
        uploadPhoto(el.target.files[0]);
    };

    return <div>

        <h4>Сменить аватарку</h4>
        <input type={'file'} onChange={mainPhotoSelected}/>
        <h4>Редактор профиля</h4>
        <form onSubmit={handleSubmit}>
        <div className={style.profileForm}>
           <label>Имя:</label>
           <Field  placeholder={profile.fullName} name='fullName' component={Input}/>

           <label>В поиске работы:</label>
           {CreateField('', Input,'lookingForAJob','checkbox' )}

           <label>Я умею:</label>
           {CreateField(profile.lookingForAJobDescription, Textarea,'lookingForAJobDescription' )}

           <label>Обо мне:</label>
           {CreateField(profile.aboutMe, Textarea,'aboutMe' )}
        </div>
       <button className={style.profileFormButton} >Сохранить</button>
    </form>
    </div>

}

const EditProfileReduxForm = reduxForm({form: 'profile'})(EditProfile);
export default EditProfileReduxForm;